:root{
    --grayish-pink: #fee0e0;
    --grayishb: #fee0e0;
    --light-yellow: #fee8ac;
    --light-blue: #e5fffa;
    --orange: #fe6a3c;
    --white: #fff;
    --black: #000;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    max-width: 100%;
    
}
.homepage{
    max-width: 395px;
    height: 100vh;
    margin: auto;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    /* align-items: flex-end; */
    background-image: url(../assets/images/homepagebackground.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 20% 20%;
    overflow: hidden;
}
.homepage p:first-child{
    font-size: 2rem;
    font-weight: 700;
    margin-top: -10px;
    margin-left: 15px;
    color: #000;
    letter-spacing: 1px;
}

.homepage p span{
    color: #697375;
    font-size: 3.8rem;
}
.homepage-content{
    background-color: var(--white);
    width: 100%;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    padding: 20px 30px;
    align-self: flex-end;
    position: relative;
    
}
.homepage-content h1{
    font-size: 2.5rem;
    font-weight: 700;
    margin: 15px 0;
}
.homepage-content > p{
    font-size: 0.9rem;
    font-weight: 600;
    color: #000
}

.next-btn{
    position: absolute;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.next-btn-icon{
    width: 40px;
    height: 40px;
}

.upcoming{
    position: relative;
    max-width: 395px;
    height: 100vh;
    margin: auto;
    background-image: url(../assets/images/3.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 20%;
    overflow: hidden;
}
.upcoming-events{
    position: relative;
    max-width: 100%;
    height: 80%;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    background-color: var(--white);
    cursor: pointer;
}
.upcoming-events > p{
    font-size: 16px;
    font-weight: 600;
    margin: 10px 10px;
}
.border{
    position: relative;
    top: 10px;
    width: 50%;
    height: 3px;
    margin: 25px auto;
    background-color: var(--black);
    border-radius: 5px;
}
.add-events{
    position: relative;
    z-index: 1;
    max-width: 100%;
    height: 80%;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    background-color: var(--white);
}
.add-event-btn{
    position: absolute;
    z-index: 10;
    bottom: 10px;
    right: 20px;
    border: none;
    outline: none;
    background: none;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px 2px rgba(0, 0, 0, 0.1);
    background-color: #e5770a;
    display: flex;
    justify-content: center;
    align-items: center;
}
.add-btn{
    width: 25px;
    height: 25px;
    fill: #fff;
    cursor: pointer;
    transition: 0.3s;
    z-index: 10;
}
.add-btn:hover{
    transform: scale(1.3) rotate(180deg);
}
.items{
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}
.upcoming-events-items{
    /* width: calc(100% / 3 - 2%); */
    height: auto;
    overflow: hidden;
    border-radius: 10px;
    padding: 5px 5px;
    border-left: 2px solid var(--grayishb);
}
.upcoming-events-items p{
    font-size: 10px;
    font-weight: 600;
}
.user-photo-div{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    overflow: hidden;
}
.user-photo-div img{
    width: 100%;
    object-fit: cover;
    position: relative;
}
.upcoming-items-inlist{
 margin-top: 20px;   
}
.upcoming-items-inlist > p{
    font-size: 12px;
    font-weight: 600;
    color: black;
    margin: 5px 10px;
}
.upcoming-list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    margin-top: 10px;
}
.list{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 94%;
    margin: auto;
}
.list > p{
    font-size: 12px;
    font-weight: 600;
    align-self: flex-start;
    margin-right: 15px;
}
.next-upcoming-events{
    width: 100%;
    padding: 10px 10px;
}
.next-upcoming-events p:first-child{
    font-size: 12px;
    font-weight: 600;
}
.next-upcoming-events p:last-child{
    font-size: 10px;
    font-weight: 500;
    color: gray;
}
.new-event-items{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5px 10px;
}
.new-event-items > p{
    color: #000;
    align-self: flex-start;
    font-size: 14px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}
.new-event-items .add-icon{
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
    margin-top: 3px;
}
.event-field{
    width: 100%;
    padding: 5px 10px;
    margin: 10px 0;
}
.event-field input{
    width: 100%;
    margin: auto;
    height: 30px;
    background: none;
    outline: none;
    border: none;
    background-color: gray;
    border-radius: 10px;
    padding: 10px 10px;
}
.event-field input::placeholder{
    color: #000;
}
.event-field div, .event-field div > p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 3px 0;
    font-weight: 600;
    font-size: 14px;
}
.alarm{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-top: 1px solid gray;
    margin-top: 20px;
}
.alarm-notifications{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}
.alarm-times{
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.alarm-times  p span:first-child{
    align-self: flex-start;
    justify-self: flex-start;
}
.details{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid gray;
    padding: 10px 0;
}
